header {
    padding: 50px;
    background-image: url(./img/job_hunt.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 400px;

}

header > form {
    margin-top: 50px;
    max-width: 400px;
}

header > h2 {
    font-size: 1.2rem;
}

.card {
    margin: 25px 0;
}

.loading-element {
    width: 100px;
    margin: 0 auto;
    height: 100px;
    display: none;
    
}

.loading-element.loading {
    display: block;
}